import { Component } from 'react';
import './App.css';
import MenuMakanan from './component/MenuMakanan/MenuMakanan';
import MenuMinuman from './component/MenuMinuman/MenuMinuman';

class App extends Component {

  constructor(){
    super();
    this.state ={
      namaResto: 'Resto Febriatala',
      namaPembeli: '',
      keterangan: '',
      jenisPembayaran: '',
      MenuMakanan: [
        {nama: 'Ayam Bakar', harga: 25000, stock: 10},
        {nama: 'Batagor', harga: 10000, stock: 0},
      ],
      MenuMinuman: [
        {id: 1, nama : 'Jus Mangga', harga: 15000},
        {id: 2, nama : 'Susu', harga: 7000},
        {id: 3, nama : 'Kopi', harga: 5000},
        {id: 4, nama : 'Jus Alpukat', harga: 15000},
        {id: 5, nama : 'Lemon tea', harga: 10000},
      ]
    }
  }

  handleGantiNama = (nama) => {
    this.setState({namaResto :nama})
  }
  
  handleGanti = (event) => {
    var name =event.target.name
    var value =event.target.value
    this.setState({[name]: value})

  }
  render() {
    return (
    <div className="App">
      <h1>Selamat Datang di {this.state.namaResto}</h1>
      <button className="btn btn-primary" onClick={()=>this.handleGantiNama('Resto Bandung')}>Ganti Nama Resto</button>
      
      <div className="row" style={{marginTop: '10px'}}>
       <div className="col-md-4 offset-md-4">
        <form>
          <div className="form-group">
            <label>Nama Pembeli:</label>
            <input className="form-control" 
            name="namaPembeli"
            onChange={this.handleGanti}
            value={this.state.namaPembeli}
            />
            <small>Nama Pembeli: {this.state.namaPembeli}</small>
          </div>
          <div className="form-group">
          <br />
            <label>Keterangan:</label>
            <textarea
                className="form-control"
                name="keterangan"
                onChange={this.handleGanti}
                value={this.state.keterangan}  
                ></textarea>
                <small>Keterangan: {this.state.keterangan}</small>
          </div>
          <br />
            <label>Jenis Pembayaran:</label>
            <select 
                className="form-control"
                name="jenisPembayaran"
                value={this.state.value} 
                onChange={this.handleGanti}
            >
            <option value="cash">Cash</option>
            <option value="kartuKredit">Kartu Kredit</option>
            <option value="voucher">Voucher</option>
            </select>
            <small>Jenis Pembayaran: {this.state.jenisPembayaran}</small>
        </form>
      </div>

      <h2>Menu Makanan</h2>
      <MenuMakanan
      namaMenu={this.state.MenuMakanan[0].nama}
      hargaMenu={this.state.MenuMakanan[0].harga}
      stock={this.state.MenuMakanan[0].stock}
      />
      <MenuMakanan
      namaMenu={this.state.MenuMakanan[1].nama}
      hargaMenu={this.state.MenuMakanan[1].harga}
      stock={this.state.MenuMakanan[1].stock}
      />
      <h2>Menu Minuman</h2>
      { this.state.MenuMinuman.map((menu)=>
        <MenuMinuman key={menu.id} namaMenu={menu.nama} hargaMenu={menu.harga}/>
        )}
    </div>
    </div>
    );
  }
}
export default App;
